@import "shared/styles";

.EditUserReservation {
	width: 100%;
	height: 100%;
	padding: 100px;
	box-sizing: border-box;
}

.inputContainer {
	display: flex;
	flex-direction: column;
	background: white;
	flex: 1 1;
	height: 100%;
	position: relative;
	padding: 30px;
	box-sizing: border-box;
	border-radius: 20px;
}

.absoluteButton {
	position: absolute;
	top: 10px;
	right: 10px;
	cursor: pointer;
}

.absoluteButtonCaption {
	font-size: 20px;
}

.upperButtonsContainer {
	display: flex;
	position: absolute;
	top: 50px;
	right: 30px;
	width: 100%;
	height: fit-content;
	gap: 10px;
	justify-content: flex-end;
}

.button {
	width: 150px;
}

.t1 {
	justify-content: flex-start;
	font-size: 26px;
}

.input {
	padding: 10px;
	box-sizing: border-box;
	border-radius: 20px;
	background-color: #F9F4ED;
	width: 250px;
}

.innerInputContainer {
	display: flex;
	align-items: center;
	gap: 10px;
	height: 60px;
}

.t2 {
	width: 150px;
	justify-content: flex-start;
}


.upperFlexContainer {
	display: flex;
	gap: 20px;
	margin-bottom: 20px;
}
